export default {
	name: 'BookSwiper',
	props: {
		booksList: {
			type: Array,
			default () {
				return []
			}
		}
	},
	data() {
		return {
			tabCur: 0,
			onTouch: false,
		}
	},
	methods: {
		changeSwiperCurrent(index) {
			this.tabCur = index
		},
		//当swiper被翻动时
		swiperChange(e){
			this.tabCur = e.detail.current
		},
		touchstart() {
			this.onTouch = true
		},
		touchend() {
			this.onTouch = false
		},
		onTapAdd(e) {
			this.$emit('selectBook', e.currentTarget.dataset.name)
		},				
		onTapRemove(e) {
			console.log(e.currentTarget.dataset.name)
			this.$emit('removeBook', e.currentTarget.dataset.name)
		}
	}
}
